import { readable, writable, derived, } from "svelte/store";
export const hotKeyMap = writable(new Map());
export const pressedKeys = derived(hotKeyMap, ($hotKeyMap) => {
    const pressedKeys = new Set();
    for (const [key, value] of $hotKeyMap.entries()) {
        if (value) {
            pressedKeys.add(key);
        }
    }
    return pressedKeys;
});
